import React from "react";
import "antd/dist/antd.css"
import {Row, Col, InputNumber, Button} from "antd"

interface AssemblyProps{
}

interface AssemblyState{
    Moral_num: number, // 
    Intelligence_num: number, // 
    Sport_num: number, // 
    Art_num: number, // 
    Labour_num: number, // 
    Total_num: number, // 
}

function Assembly(props: AssemblyProps, state: AssemblyState){
    const [Moral_num, set_Moral_num] = React.useState(0)
    const [Intelligence_num, set_Intelligence_num] = React.useState(0)
    const [Sport_num, set_Sport_num] = React.useState(0)
    const [Art_num, set_Art_num] = React.useState(0)
    const [Labour_num, set_Labour_num] = React.useState(0)
    const [Total_num, set_Total_num] = React.useState(0)
    let line_height = 30

    function onChange_Moral(value: number){
        set_Moral_num(value)
    }

    function onChange_Intelligence(value: number){
        set_Intelligence_num(value)
    }

    function onChange_Sport(value: number){
        set_Sport_num(value)
    }

    function onChange_Art(value: number){
        set_Art_num(value)
    }

    function onChange_Labour(value: number){
        set_Labour_num(value)
    }

    function get_total(){
        let total = Moral_num * 0.2 + Intelligence_num * 0.5 + Sport_num * 0.1 + Art_num * 0.1 + Labour_num * 0.1
        console.log(total)
        total = Number(total.toFixed(2))
        console.log(total)
        set_Total_num(total)
    }

    return (
        <div className="Assembly" style={{position:'absolute', width: "100%", height: '70vh', top: 55}}>
            <div style={{float: 'left', width: 180, height: 300, marginLeft: "42%", fontSize: '15px', lineHeight: line_height + "px", fontWeight: 600}}>
                <div style={{float: 'left', height: line_height}}>
                    <div style={{float: 'left', height: line_height}}>(20%):&nbsp;</div>
                    <div style={{float: 'left', height: line_height}}>
                        <InputNumber min={0} max={100} step={0.01} value={Moral_num} onChange={onChange_Moral as any}></InputNumber>
                    </div>
                </div>
                <div style={{float: 'left', height: line_height, marginTop: 10}}>
                    <div style={{float: 'left', height: line_height}}>(50%):&nbsp;</div>
                    <div style={{float: 'left', height: line_height}}>
                        <InputNumber min={0} max={100} step={0.01} value={Intelligence_num} onChange={onChange_Intelligence as any}></InputNumber>
                    </div>
                </div>
                <div style={{float: 'left', height: line_height, marginTop: 10}}>
                    <div style={{float: 'left', height: line_height}}>(10%):&nbsp;</div>
                    <div style={{float: 'left', height: line_height}}>
                        <InputNumber min={0} max={100} step={0.01} value={Sport_num} onChange={onChange_Sport as any}></InputNumber>
                    </div>
                </div>
                <div style={{float: 'left', height: line_height, marginTop: 10}}>
                    <div style={{float: 'left', height: line_height}}>(10%):&nbsp;</div>
                    <div style={{float: 'left', height: line_height}}>
                        <InputNumber min={0} max={100} step={0.01} value={Art_num} onChange={onChange_Art as any}></InputNumber>
                    </div>
                </div>
                <div style={{float: 'left', height: line_height, marginTop: 10}}>
                    <div style={{float: 'left', height: line_height}}>(10%):&nbsp;</div>
                    <div style={{float: 'left', height: line_height}}>
                        <InputNumber min={0} max={100} step={0.01} value={Labour_num} onChange={onChange_Labour as any}></InputNumber>
                    </div>
                </div>
                <div style={{float: 'left', height: line_height, width: 170, marginTop: 10, display: "flex", justifyContent: 'center', alignItems: 'center', flexDirection: 'column'}}>
                    <Button type="primary" onClick={get_total}></Button>
                </div>
                <div style={{float: 'left', height: line_height, marginTop: 10}}>
                    <div style={{float: 'left', height: line_height, width: 170, textAlign:'center'}}>:&nbsp;{Total_num}</div>
                    {/* <div style={{float: 'left', height: line_height}}>
                        <InputNumber></InputNumber>
                    </div> */}
                </div>
            </div>
        </div>
    )
}

export default Assembly